function wxOpenLocation(latitude, longitude, name, address) {
  wx.openLocation({
    latitude: latitude, // 纬度，浮点数，范围为90 ~ -90
    longitude: longitude, // 经度，浮点数，范围为180 ~ -180。
    name: name, // 位置名
    address: address, // 地址详情说明
    scale: 20, // 地图缩放级别,整形值,范围从1~28。默认为最大
    infoUrl: '' // 在查看位置界面底部显示的超链接,可点击跳转
  });
}

function wxUploadImage(callback) {
  wx.chooseImage({
    count: 1, // 默认9
    sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
    sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
    success: function success(res) {
      //var localIds = res.localIds;               // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
      var localId = res.localIds[0];

      wx.uploadImage({
        localId: localId.toString(), // 需要上传的图片的本地ID，由chooseImage接口获得
        isShowProgressTips: 1, // 默认为1，显示进度提示
        success: function success(res) {
          // var serverId = res.serverId;           // 返回图片的服务器端ID
          callback(res);
        },
        fail: function fail(res) {
          // alert(JSON.stringify(res));
        }
      });
    }
  });
}

function wxScanQRCodes(callback) {
  wx.checkJsApi({
    jsApiList: ['scanQRCode'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
    success: function success(res) {
      // 以键值对的形式返回，可用的api值true，不可用为false
      // 如：{"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
      if (res.checkResult.scanQRCode) {
        wx.scanQRCode({
          needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
          scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
          success: function success(ret) {
            // var result = res.resultStr;            // 当needResult 为 1 时，扫码返回的结果
            if (typeof callback == "function") {
              callback(ret.resultStr);
            }
          }
        });
      }
    }
  });
}

// 预览图片接口
function wxPreviewImage(current, urls) {
  wx.previewImage({
    current: current, // 当前显示图片的http链接
    urls: urls // 需要预览的图片http链接列表
  });
}

